import React from 'react'

function OldNewRuleComponent() {
    return (
        <>
            <div className='p-4'>
                <h1 className='text-3xl text-center text-violet-600 underline'>RULES</h1>
                <div className=''>
                    <h1 className='text-violet-600 font-semibold p-4 text-xl underline'> Old Rule</h1>
                    <h1 className='text-violet-600 text-md p-4'>According to old rule if property is of before 2013 then license will be granted only if any objection has not been received within 15 days from the local residence and any , if any objection has been received then  committee headed by Municipal Commissioner will take appropriate decision.</h1>
                </div>
                <div className=''>
                    <h1 className='text-violet-600 font-semibold p-4 text-xl underline'> New Rule</h1>
                    <h1 className='text-violet-600 text-md p-4'>According to new rule if property is of after 2013 license will be granted only when building plans is approved by ULB .</h1>
                </div>
            </div>
        </>
    )
}

export default OldNewRuleComponent